@font-face {
    font-family: 'PokemonFont';
    src: url('../fonts/AccordAlternate-Bold.woff'), 
         url('../fonts/AccordAlternate-Bold.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
	width: 100%;
	height: 100%;
	font-family: 'PokemonFont';
	font-size: 22px;
	text-align: center; 

}

#container {
    margin: auto;
    text-align: center;
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

.box {
    display: block;
    width: 300px;
    padding: 15px 0 15px 0;
    margin: 5px auto;
    margin-bottom: 7px;
    cursor: pointer;
    color: white;
	border-color: red;
	border: 2px solid rgb(136, 130, 130);
    text-decoration: none;
    font-size: 22px;
    font-weight: bold;
}
.box:first-child {
    border-radius: 10px 10px 0 0;
}
.box:last-child {
    border-radius: 0 0 10px 10px;
}
.box:hover {
	transform: scale(1.1);
	font-weight:bold;
}

.red { background-color: #ee5c52; }
.red:hover    { background-color: #c25850; border-color: #813a35; ;}
.green  { background-color: #1bcf69; }
.green:hover  { background-color: #28a15c; border-color: #1b7241;}

.blue   { background-color: #63a0d4; }
.blue:hover   { background-color: #6090b9; }

.black   { background-color: #282b2e; }
.black:hover   { background-color: #202122; }

.gold   { background-color: rgb(206, 179, 58);  }
.gold:hover   { background-color: #a38544;}

#projects_header {

  top: 50px;
  text-align: center;
  background-color: rgb(11,9,25);

  /*Font used in the header*/
  font-family:'PokemonFont';
  color: rgba(228, 222, 190, 0.856);
  text-shadow: 1px 1px rgb(236, 231, 229);
  font-size: 58px;
  /*Padding to move the text to a good position*/
  padding-top: 5px;
  padding-bottom: 10px;
  border-radius: 5px;
  border: 5px solid rgb(21, 15, 56);
  background-blend-mode: lighten;
}


/* Star Background from: https://codepen.io/stack-findover/pen/eYWPwPV */

.bg {
    background: url(https://i.ibb.co/87GbbFP/2799006.jpg) no-repeat;
    background-size: cover;
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -3;
}
.bg:before {
    content: "";
    width: 100%;
    height: 100%;
    background: #000;
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    opacity: 0.3;
}
@keyframes sf-fly-by-1 {
    from {
        transform: translateZ(-600px);
        opacity: 0.5;
    }
    to {
        transform: translateZ(0);
        opacity: 0.5;
    }
}
@keyframes sf-fly-by-2 {
    from {
        transform: translateZ(-1200px);
        opacity: 0.5;
    }
    to {
        transform: translateZ(-600px);
        opacity: 0.5;
    }
}
@keyframes sf-fly-by-3 {
    from {
        transform: translateZ(-1800px);
        opacity: 0.5;
    }
    to {
        transform: translateZ(-1200px);
        opacity: 0.5;
    }
}
.star-field {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    perspective: 600px;
    -webkit-perspective: 600px;
    z-index: -1;
